import { DateTime } from "luxon";
import { GameSnapshot, GameState, WordleAction, WordleHistoryEntry, WordleState } from "./types";
import { getCharacterIndicesMapForWord, getInitialCharacterStatusMap, getInitialGuesses } from "./utils";

export const wordleReducer = (state: WordleState, action: WordleAction): WordleState => {
  switch (action.type) {
    case "numGuesses.update":
      return {
        ...state,
        currentState: {
          ...state.currentState,
          numGuesses: action.newNumGuesses,
          guesses: getInitialGuesses(action.newNumGuesses, state.currentState.numCharacters),
        },
      };
    case "numCharacters.update":
      return {
        ...state,
        currentState: {
          ...state.currentState,
          numCharacters: action.newNumCharacters,
          guesses: getInitialGuesses(state.currentState.numGuesses, action.newNumCharacters),
        },
      };
    case "gameState.update":
      return {
        ...state,
        currentState: getUpdatedStateAfterGameStateUpdate(state.currentState, action.newGameState, action.newWord),
      };
    case "snackbar.show":
      return { ...state, message: action.message, notificationOpen: true };
    case "snackbar.hide":
      return { ...state, notificationOpen: false };
    case "backspace.pressed": {
      const updatedGuesses = [...state.currentState.guesses];
      const currentGuessIndex = state.currentState.currentGuessIndex;
      const currentCharacterIndex = state.currentState.currentCharacterIndex;
      updatedGuesses[currentGuessIndex].characters[currentCharacterIndex - 1] = { character: "" };
      return {
        ...state,
        currentState: {
          ...state.currentState,
          guesses: updatedGuesses,
          currentCharacterIndex: currentCharacterIndex - 1,
        },
      };
    }
    case "enter.pressed":
      if (action.errorMessage) {
        const updatedGuesses = [...state.currentState.guesses];
        updatedGuesses[state.currentState.currentGuessIndex].status = "invalid";
        return {
          ...state,
          currentState: {
            ...state.currentState,
            guesses: updatedGuesses,
          },
          message: action.errorMessage,
          notificationOpen: true,
        };
      }
      return getUpdatedStateAfterEnterPressed(state);
    case "aToZ.pressed": {
      const updatedGuesses = [...state.currentState.guesses];
      const currentGuessIndex = state.currentState.currentGuessIndex;
      const currentCharacterIndex = state.currentState.currentCharacterIndex;
      updatedGuesses[currentGuessIndex].characters[currentCharacterIndex] = {
        character: action.key,
        status: "unknown",
      };
      return {
        ...state,
        currentState: {
          ...state.currentState,
          guesses: updatedGuesses,
          currentCharacterIndex: currentCharacterIndex + 1,
        },
      };
    }
    case "historyEntry.restore":
      return { ...state, currentState: action.savedGameSnapshot };
    case "history.clear":
      return { ...state, history: [], message: "History cleared", notificationOpen: true };
    default: {
      throw new Error();
    }
  }
};

function getUpdatedStateAfterGameStateUpdate(
  currentState: GameSnapshot,
  newGameState: GameState,
  newWord?: string
): GameSnapshot {
  const updatedState = { ...currentState, gameState: newGameState };
  if (newGameState === "new") {
    return {
      ...updatedState,
      guesses: getInitialGuesses(currentState.numGuesses, currentState.numCharacters),
      characterStatusMap: getInitialCharacterStatusMap(),
      currentGuessIndex: 0,
      currentCharacterIndex: 0,
    };
  } else if (newGameState === "in_progress") {
    return {
      ...updatedState,
      word: newWord!,
      characterIndicesMap: getCharacterIndicesMapForWord(newWord!),
    };
  }
  return updatedState;
}

function getUpdatedStateAfterEnterPressed(state: WordleState): WordleState {
  const updatedGuesses = [...state.currentState.guesses];
  const updatedCharacterIndicesMap = getUpdatedCharacterIndicesMap(state.currentState);
  const updatedCharacterStatusMap = new Map(state.currentState.characterStatusMap);
  let numCorrect = 0;
  for (let i = 0; i < state.currentState.numCharacters; i++) {
    const currentCharacter = updatedGuesses[state.currentState.currentGuessIndex].characters[i];
    if (state.currentState.word[i] === currentCharacter.character) {
      currentCharacter.status = "correct";
      numCorrect++;
    } else if (updatedCharacterIndicesMap.has(currentCharacter.character)) {
      currentCharacter.status = "wrong_position";
    } else {
      currentCharacter.status = "not_used";
    }
    if (
      currentCharacter.status === "correct" ||
      currentCharacter.status === "not_used" ||
      updatedCharacterStatusMap.get(currentCharacter.character) === "unknown"
    ) {
      updatedCharacterStatusMap.set(currentCharacter.character, currentCharacter.status!);
    }
  }
  const updatedCurrentState = {
    ...state.currentState,
    guesses: updatedGuesses,
    characterIndicesMap: updatedCharacterIndicesMap,
    characterStatusMap: updatedCharacterStatusMap,
    currentGuessIndex: state.currentState.currentGuessIndex + 1,
    currentCharacterIndex: 0,
  };
  if (numCorrect === state.currentState.numCharacters) {
    updatedCurrentState.guesses[state.currentState.currentGuessIndex].status = "won";
    return {
      currentState: {
        ...updatedCurrentState,
        gameState: "won",
      },
      history: getUpdatedHistory(state, "won"),
      message: "Well done!",
      notificationOpen: true,
    };
  } else if (state.currentState.currentGuessIndex === state.currentState.numGuesses - 1) {
    updatedCurrentState.guesses[state.currentState.currentGuessIndex].status = "";
    return {
      currentState: {
        ...updatedCurrentState,
        gameState: "lost",
      },
      history: getUpdatedHistory(state, "lost"),
      message: state.currentState.word,
      notificationOpen: true,
    };
  }
  return { ...state, currentState: updatedCurrentState };
}

function getUpdatedCharacterIndicesMap(currentState: GameSnapshot) {
  const updatedCharacterIndicesMap = new Map(currentState.characterIndicesMap);
  for (let i = 0; i < currentState.numCharacters; i++) {
    if (
      currentState.word[i] === currentState.guesses[currentState.currentGuessIndex].characters[i].character &&
      updatedCharacterIndicesMap.has(currentState.word[i])
    ) {
      updatedCharacterIndicesMap.get(currentState.word[i])!.delete(i);
      if (updatedCharacterIndicesMap.get(currentState.word[i])!.size === 0) {
        updatedCharacterIndicesMap.delete(currentState.word[i]);
      }
    }
  }
  return updatedCharacterIndicesMap;
}

function getUpdatedHistory(state: WordleState, newGameState: GameState) {
  const numAttempts = newGameState === "won" ? state.currentState.currentGuessIndex + 1 : "X";
  const updatedHistory: WordleHistoryEntry[] = [
    {
      state: {
        ...state.currentState,
        gameState: "saved",
      },
      result: `${numAttempts}/${state.currentState.numGuesses}`,
      date: DateTime.now().toLocaleString(DateTime.DATETIME_SHORT),
    },
    ...state.history,
  ];
  return updatedHistory;
}
