import {
  CharacterStatus,
  GameSnapshot,
  Guess,
  GuessCharacter,
  Settings,
  WordleHistoryEntry,
  WordleState,
} from "./types";

export function getWordsMap(wordsString: string, minLen: number, maxLen: number) {
  const wordsMap = new Map<number, string[]>();
  for (let i = minLen; i <= maxLen; i++) {
    wordsMap.set(i, []);
  }
  const listOfWords = wordsString.split(/\s+/);
  for (const word of listOfWords) {
    if (word.length >= minLen && word.length <= maxLen) {
      wordsMap.get(word.length)!.push(word.toUpperCase());
    }
  }
  return wordsMap;
}

export function removeWordsNotInAllwords(wordsMap: Map<number, string[]>, allwordsMap: Map<number, string[]>) {
  for (const wordLen of Array.from(wordsMap.keys())) {
    const filteredWords: string[] = [];
    for (let j = 0; j < wordsMap.get(wordLen)!.length; j++) {
      if (allwordsMap.get(wordLen)!.includes(wordsMap.get(wordLen)![j])) {
        filteredWords.push(wordsMap.get(wordLen)![j]);
      }
    }
    wordsMap.set(wordLen, filteredWords);
  }
}

export function getInitialGuesses(numGuesses: number, numCharacters: number) {
  const guesses: Guess[] = [];
  for (let i = 0; i < numGuesses; i++) {
    const guessCharacters: GuessCharacter[] = [];
    for (let j = 0; j < numCharacters; j++) {
      guessCharacters.push({ character: "" });
    }
    guesses.push({ characters: guessCharacters, status: "" });
  }
  return guesses;
}

export function getInitialCharacterStatusMap() {
  const characterStatusMap = new Map<string, CharacterStatus>();
  for (let ch = 0; ch < 26; ch++) {
    characterStatusMap.set(String.fromCharCode(65 + ch), "unknown");
  }
  return characterStatusMap;
}

export function getCharacterIndicesMapForWord(word: string) {
  const characterIndicesMapForWord = new Map<string, Set<number>>();
  for (let i = 0; i < word.length; i++) {
    if (!characterIndicesMapForWord.has(word[i])) {
      characterIndicesMapForWord.set(word[i], new Set<number>());
    }
    characterIndicesMapForWord.get(word[i])!.add(i);
  }
  return characterIndicesMapForWord;
}

export function getInitialState(): WordleState {
  const initialCurrentState: GameSnapshot = window.localStorage.getItem("wordleState")
    ? JSON.parse(window.localStorage.getItem("wordleState")!, reviver)
    : {
        numGuesses: 6,
        numCharacters: 5,
        guesses: getInitialGuesses(6, 5),
        characterStatusMap: getInitialCharacterStatusMap(),
        word: "",
        characterIndicesMap: new Map<string, Set<number>>(),
        gameState: "new",
        currentGuessIndex: 0,
        currentCharacterIndex: 0,
      };
  const initialHistory = window.localStorage.getItem("wordleHistory")
    ? (JSON.parse(window.localStorage.getItem("wordleHistory")!, reviver) as WordleHistoryEntry[])
    : [];
  return {
    currentState: initialCurrentState,
    history: initialHistory,
    notificationOpen: false,
    message: "",
  };
}

export function getInitialSettings(): Settings {
  return window.localStorage.getItem("wordleSettings")
    ? JSON.parse(window.localStorage.getItem("wordleSettings")!)
    : { hardMode: false, darkTheme: false };
}

export function replacer(_: string, value: any) {
  if (value instanceof Map) {
    return {
      dataType: "Map",
      value: Array.from(value),
    };
  } else if (value instanceof Set) {
    return {
      dataType: "Set",
      value: Array.from(value),
    };
  } else {
    return value;
  }
}

export function reviver(_: string, value: any) {
  if (typeof value === "object" && value !== null) {
    if (value.dataType === "Map") {
      return new Map(value.value);
    } else if (value.dataType === "Set") {
      return new Set(value.value);
    }
  }
  return value;
}
