import { Button, InputLabel, MenuItem, Select, TextField } from "@mui/material";
import { ThemeProvider } from "@mui/material/styles";
import React from "react";
import { ToWords } from "to-words";
import { maxWordLen, minWordLen } from "../constants";
import { ToolbarProps } from "../types";

const toWords = new ToWords();

export const ToolbarComponent: React.FC<ToolbarProps> = (props) => {
  return (
    <ThemeProvider theme={props.theme}>
      <div className="toolbar">
        <div className="toolbar_left">
          {props.gameState === "new" && (
            <div>
              <div className="num_guesses_setting">
                <InputLabel sx={{ color: "var(--color-tone-1)" }}>Number of Guesses:</InputLabel>
                <TextField
                  size="small"
                  value={props.numGuesses}
                  onChange={props.onGuessesChanged}
                  sx={{ marginLeft: "8px", width: 50 }}
                ></TextField>
              </div>
              <div className="num_characters_setting">
                <InputLabel sx={{ color: "var(--color-tone-1)" }}>Word Length:</InputLabel>
                <Select size="small" value={props.numCharacters} onChange={props.onCharactersChanged}>
                  {Array.from({ length: maxWordLen - minWordLen + 1 }, (_, i) => i + minWordLen).map((i) => (
                    <MenuItem key={i} value={i}>
                      {toWords.convert(i)}
                    </MenuItem>
                  ))}
                </Select>
              </div>
            </div>
          )}
          {(props.gameState === "won" || props.gameState === "lost") && (
            <Button
              variant="contained"
              size="small"
              onClick={props.share}
              sx={{
                marginTop: "8px",
                marginBottom: "8px",
                background: "#6aaa64",
                ":hover": {
                  background: "#6aaa64",
                  opacity: 0.9,
                },
              }}
            >
              Share
            </Button>
          )}
        </div>
        <div className="spacer"></div>
        <div className="toolbar_right">
          {props.gameState === "new" ? (
            <Button
              variant="contained"
              size="small"
              onClick={props.start}
              sx={{ marginTop: "8px", marginBottom: "8px" }}
            >
              Start
            </Button>
          ) : (
            <Button
              variant="contained"
              size="small"
              onClick={props.reset}
              sx={{ marginTop: "8px", marginBottom: "8px" }}
            >
              Reset
            </Button>
          )}
        </div>
      </div>
    </ThemeProvider>
  );
};
