import React from "react";
import { Guess, GuessCharacter } from "../types";

export const GuessesComponent: React.FC<{ guesses: Guess[] }> = ({ guesses }) => {
  return (
    <div className="guesses">
      {guesses.map((guess, i) => (
        <GuessComponent key={`guess_${i}`} guess={guess}></GuessComponent>
      ))}
    </div>
  );
};

const GuessComponent: React.FC<{ guess: Guess }> = ({ guess }) => {
  return (
    <div className={`guess_row ${guess.status}`}>
      {guess.characters.map((guessCharacter, i) => (
        <GuessCharacterComponent
          key={`guess_character_${i}`}
          guessCharacter={guessCharacter}
          numCharacters={guess.characters.length}
        ></GuessCharacterComponent>
      ))}
    </div>
  );
};

const GuessCharacterComponent: React.FC<{ guessCharacter: GuessCharacter; numCharacters: number }> = ({
  guessCharacter,
  numCharacters,
}) => {
  return guessCharacter.character === "" ? (
    <div className={`guess_character_div_${numCharacters} guess_character_empty`}></div>
  ) : guessCharacter.status === "unknown" ? (
    <div className={`guess_character_div_${numCharacters} guess_character guess_character_unknown`}>
      {guessCharacter.character}
    </div>
  ) : (
    <div className={`guess_character_div_${numCharacters} guess_character guess_character_${guessCharacter.status}`}>
      {guessCharacter.character}
    </div>
  );
};
